@import url('https://fonts.googleapis.com/css2?family=Open+Sans:ital,wght@0,300;0,400;0,500;0,600;0,700;0,800;1,300;1,400;1,500;1,600;1,700;1,800&display=swap');

/* ------------- GLOBAL CONFIGS -----------*/

*{
    box-sizing: border-box;
    margin: 0;
}

:root{
    --branco:#F8F8F8;
    --cinzaClaro:#D2D2D2;
    --cinzaEscuro: #303030;
    --preto: #191919;
    --preto2: #232323;
    --verm1: #802424;
    --verm2: #FF4747;
    --verm3: #FF9494;
}

body{
    background-color: var(--preto);
    color: var(--branco);
    font-family: 'Open Sans', sans-serif;
}

.lightTheme{
    background-color: var(--branco);
    color: var(--preto);
    height: 100vh;
}

.dark{
    color: var(--branco);
}

/* ------------- NAVBAR -----------*/

nav{
    background-color: var(--branco);
    height: 70px;
    width: 100%;
    box-shadow: 2px 2px 5px rgba(0, 0, 0, 0.185);
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding-left: 10px;
    padding-right: 10px;
}



nav.dark{
    background-color: var(--preto2);
    box-shadow: 2px 2px 5px rgba(0, 0, 0, 0.411);
}

/* NAVBAR LINKS*/

nav a {
    text-decoration: none;
    color: var(--preto);
    padding-left: 40px;
    font-size: 14px;
    user-select: none;
}

nav a.dark{
    color: var(--branco)
}

nav a:hover {  
    color: var(--verm2); 
    font-size: 16px;
    font-weight: bolder;
    transition: 0.2s ease-in-out;
}

nav a.dark:hover {  
    color: var(--verm1); 
    font-size: 16px;
    font-weight: bolder;
    transition: 0.2s ease-in-out;
}

/* ------------- SWITCH BUTTON DARKMODE -----------*/

.switches--container{
    display: flex;
    justify-content: space-between;
}

.switch--container{
    margin-left: 10px;
    height: 100%;
    width: 80px;
    display: flex;
    align-items: center;
    justify-content: center;
}

.switch{
    position: relative;

    height: 40px;
    width: 80px;
    background-color: var(--cinzaClaro);
    border-radius: 40px;
    box-shadow: 2px 2px 2px rgba(0, 0, 0, 0.226);
    transition: 0.5s;
}

.switch--dark{
    background-color: var(--cinzaEscuro);
    box-shadow: -2px 2px 2px rgba(0, 0, 0, 0.507);
    transition: 0.5s;
}

.switch--button{
    position: absolute;
    height: 30px;
    width: 50px;
    top: 5px;
    left: 5px;
    background-color: var(--branco);
    border-radius: 25px;
    transition: 0.5s;
    font-size: 12px;
    font-weight: bolder;
    color: var(--preto);
    display: flex;
    align-items: center;
    justify-content: center;
    transition: 0.5s;
    cursor: pointer;
    user-select: none;
    box-shadow: 3px 1px 2px rgba(0, 0, 0, 0.226);
}

.switch--button:hover{
    transform: scale(1.025);
    box-shadow: 4px 2px 2px rgba(0, 0, 0, 0.226);
}

.switch--button--dark{
    background-color: var(--preto2);
    color: var(--branco);
    box-shadow: -3px 1px 2px rgba(0, 0, 0, 0.507);
    transition: 0.5s;
}

.switch--button--dark:hover{
    transform: scale(1.025);
    box-shadow: -4px 1px 2px rgba(0, 0, 0, 0.507);
}

.switch--button--state{
    left:25px;
}

/* ------------- TITLE -----------*/

.title{
    margin: 20px 10px 10px 100px;
    position: relative;
}

.title h3 {
    font-size: 30px;
    font-weight: normal;
}

.title h2 {
    font-size: 40px;
    font-weight: normal;
}

.title h1 {
    font-size: 70px;
}

.title button {
    position: relative;
    background-color: var(--verm1);
    background-image: linear-gradient(to right, var(--verm3), var(--verm2));
    color: var(--branco);
    font-size: 18px;
    height: 40px;
    border-radius: 0px;
    border: none;
    cursor: pointer;
    margin-top: 30px;
    box-shadow: 4px 4px 0px black;
    transition: 0.5s;
}

.title button.dark {
    box-shadow: 4px 4px 0px var(--branco);
}

.title button:hover {
    transform: translate(4px,4px);
    box-shadow: none;
    transition: 0.5s ;
}

.title button:active {
    transform: translate(8px,8px) scale(0.98);
    box-shadow: inset 4px 4px 10px black;
    transition: 0.5s;
}

.title button.dark:active {
    transform: translate(8px,8px);
    box-shadow: inset 4px 4px 10px black;
    transition: 0.5s;
}








